.mode-toggle {
    position: fixed;
    top: 10px;
    right: 10px;
  }
  
  #toggle-mode {
    background-color: #282c34; /* Dark mode button color */
    color: #ffffff; /* Dark mode text color */
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    transition: background-color 0.3s, color 0.3s;
  }
  
  .light-mode #toggle-mode {
    background-color: #ffffff; /* Light mode button color */
  }
  
  